
body {
    margin: 0px 10px 50px;
}

.header_name {
    font: 27px 'Franklin Gothic Demi';
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
}

.header_other {
    font: 13px 'Franklin Gothic Demi';
    margin-left: 5px;
    margin-right: 5px;
    text-align: center;
}

.headerCircle {
    display: none;
    background: #FFFFFF;
    border-radius: 50%;
    z-index: 100;
}

.headerBox {
    width: 100%;
}

.headerText {
    text-transform: uppercase;
    position: relative;
    margin-left: 5px;
    margin-right: 5px;
    top: 25%;
    bottom: 25%;
}

.sectionTitle {
    font: 19px 'Franklin Gothic Demi';
    text-transform: uppercase;

}

.sectionBar {
    display: block;
    /*width: 90%;*/
    height: 10px;
    margin-bottom: 10px;
}

.sectionContainer {
    margin: 0 20px 0 20px;
}
.sectionContent {
    /*width: 90%;*/
}

.description {
    font: 13px 'Rockwell';
}

.headline {
    font: 13px 'Franklin Gothic Demi';
    margin-bottom: 5px;
    text-transform: uppercase;
}

.ft0 {
    font: 73px 'Franklin Gothic Demi';
    color: #ea4e4e;
}

.ft1 {
    font: 19px 'Franklin Gothic Demi';
}

#sidebar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

#contentbar {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

@media only screen and (min-width: 768px) {
    .header_name {
        font: 33px 'Franklin Gothic Demi';
        text-align: right;
    }

    .header_other {
        font: 15px 'Franklin Gothic Demi';
        text-align: right;
    }

    #contentbar {
        width: 65%;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    #sidebar {
        width: 35%;
        display: -ms-inline-flexbox;
        display: inline-flex;
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .headerCircle {
        display: -ms-inline-flexbox;
        display: inline-flex;
    }

    .headerBox {
        margin-left: 5%;
        margin-right: 5%;
        width: 90%;
        position: absolute;
        top: 50px;
    }

    .headerText {
        text-align: right;
        margin-right: 10px;
    }
}




